.box{
    width:100%;
    height:80px;
    display: flex;
    padding:0 20px;
    .left{
        flex:8;
        display: flex;
        .button{
            height:100%;
            width:100px;
            display: flex;
            align-items: center;
            justify-content: center;
             button{
                background:#D8D8D8;
                border-color: #D8D8D8;
                color:#A6A6A6;
        }
        }
        ul{
             height:100%;
             min-width:100px;
             list-style:none;
             display: flex;
             align-items: center;
            li{
                padding:5px 10px;
                cursor: pointer;
                margin:0 5px;

            } 
            .active{
                background: #c5cfea;
            }
         }
         form{
             height:100%;
             display: flex;
             align-items: center;
             .ant-form-item{
                 height:100%;
                 margin-top:3%;
                 display: flex;
                 align-items: center;
                 .ant-form-item-control-wrapper{
                    margin-top:4%;
                 }
             }
         }
    }
    .right{
        flex: 2;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        button{
            background:#fff;
            border-color: #fff;
            color:#000;
        }
    }
}
.all-bottom{
    width: 100%;
    min-height:100px;
    display: flex;

    .all-left{
        width:80%;
        float: left;
        display: flex;
       flex-direction: column;
       .all-left-top{
           flex:1;
           display: flex;
           align-items: center;
           padding-top:2%;
           .ant-form-item{
               min-width: 200px;
               float: left;
               padding:0 20px;
           }
       }
       .all-left-bottom{
           flex:1;
           padding-top:2%;
           .ant-form-item{
               min-width: 200px;
               float: left;
               padding:0 17px;
               display: flex;
               align-items: center;
           }
       }
    }
    .all-right{
        width:20%;
        float: left;
        display: flex;
       align-items: center;
       justify-content: flex-end;
       padding:0 10px;
       height:184.7px;
       button{
           margin-left:10px;
       }
       button:nth-child(2){
           background:#D8D8D8;
           border-color: #D8D8D8;
           color:blue;
       }
   } 
}
